.footer{background:rgba(30,30,30,.85);background-image:url("../../../public/3.jpg");background-size:cover;background-position:center;background-repeat:no-repeat;backdrop-filter:blur(15px) saturate(1.2);box-shadow:0 -2px 20px rgba(0,0,0,.8);border-top:none;position:sticky;bottom:0;z-index:100}.footer .footer-content{max-width:1200px;margin:0 auto;display:flex;justify-content:space-between;align-items:center;padding:1rem 2rem;height:80px}.footer .footer-content p{color:#fff;font-family:"Courier New",monospace;font-size:.9rem;margin:0}.footer .footer-content .footer-left p{color:#666;font-weight:700;transform:translateY(16px);text-shadow:.5px .5px 0 #333,1px 1px 0 #222,1.5px 1.5px 0 #111,2px 2px 0 #000,-0.5px -0.5px 0 #888,-1px -1px 0 #999,-1.5px -1.5px 0 #aaa;letter-spacing:1px}.footer .footer-content .footer-center{text-align:center;transform:translateY(-26px)}.footer .footer-content .footer-center .footer-navigation{display:flex;justify-content:space-between;align-items:center;gap:10px}.footer .footer-content .footer-center .footer-navigation .footer-task{color:#666;font-size:.8rem;margin:0;flex:0 0 auto;filter:grayscale(100%) brightness(0.7) contrast(1.2)}.footer .footer-content .footer-center .footer-navigation .footer-main{color:orange;font-weight:500;margin:0;flex:1;text-align:center}.footer .footer-content .footer-center .footer-navigation .footer-main::before{content:"📄";filter:grayscale(100%) brightness(0.7) contrast(1.2)}.footer .footer-content .footer-center .footer-navigation .footer-contacts{color:#666;font-size:.8rem;margin:0;flex:0 0 auto;filter:grayscale(100%) brightness(0.7) contrast(1.2)}.footer .footer-content .footer-right{text-align:right}.footer .footer-content .footer-right p{color:#666;font-weight:400}@media(max-width: 768px){.footer .footer-content{flex-direction:column;gap:.5rem;padding:1rem;height:auto}.footer .footer-content .footer-left,.footer .footer-content .footer-center,.footer .footer-content .footer-right{text-align:center}}