.error-message{background:linear-gradient(135deg, #ff4757, #ff3838);color:#fff;padding:15px 20px;border-radius:8px;margin:20px 0;display:flex;align-items:center;box-shadow:0 4px 15px rgba(255,71,87,.3);animation:slideIn .3s ease-out}.error-message .error-icon{font-size:1.2rem;margin-right:10px}.loading-message{display:flex;flex-direction:column;align-items:center;justify-content:center;padding:40px 20px;color:#666}.loading-message .loading-spinner{font-size:2rem;margin-bottom:10px;animation:spin 2s linear infinite}.loading-message p{font-size:1.1rem;margin:0}.no-results,.no-operations{display:flex;flex-direction:column;align-items:center;justify-content:center;padding:60px 20px;color:#666;text-align:center}.no-results .no-results-icon,.no-results .no-operations-icon,.no-operations .no-results-icon,.no-operations .no-operations-icon{font-size:3rem;margin-bottom:15px;opacity:.7}.no-results h3,.no-operations h3{margin:0 0 10px 0;color:#555}.no-results p,.no-operations p{margin:0;font-size:1rem}@keyframes slideIn{from{opacity:0;transform:translateY(-10px)}to{opacity:1;transform:translateY(0)}}@keyframes spin{from{transform:rotate(0deg)}to{transform:rotate(360deg)}}.btn-submit.loading,.login-button.loading{opacity:.7;cursor:not-allowed}.btn-submit.loading .loading-spinner,.login-button.loading .loading-spinner{animation:spin 1s linear infinite}@media(max-width: 768px){.error-message{padding:12px 15px;font-size:.9rem}.loading-message{padding:30px 15px}.loading-message .loading-spinner{font-size:1.5rem}.no-results,.no-operations{padding:40px 15px}.no-results .no-results-icon,.no-results .no-operations-icon,.no-operations .no-results-icon,.no-operations .no-operations-icon{font-size:2.5rem}}.App{min-height:100vh;background-image:url("../../public/background.png");background-size:cover;background-position:center;background-repeat:no-repeat;background-attachment:fixed;font-family:"Courier New","Monaco",monospace;display:flex;flex-direction:column}.App .main-content{padding:20px;width:60%;margin-left:30%;margin-right:0;flex:1;padding-bottom:100px}*{margin:0;padding:0;box-sizing:border-box}body{font-family:"Courier New","Monaco",monospace;line-height:1.6;color:#fff;background:rgba(0,0,0,0)}.users-and-groups-page{padding:2rem;margin-left:20%}.users-and-groups-page h2{color:#90ee90;font-size:2rem;margin-bottom:1rem;font-family:"Courier New",monospace;font-weight:700;position:relative;display:flex;align-items:center;justify-content:center;gap:1rem}.users-and-groups-page h2::before{content:"☢";color:#ff8c00;font-size:2.5rem;animation:pulse 2s infinite}.users-and-groups-page h2::after{content:"";position:absolute;bottom:-10px;left:50%;transform:translateX(-50%);width:100px;height:2px;background:linear-gradient(90deg, transparent, #90EE90, transparent)}.users-and-groups-page p{color:#90ee90;font-size:1.2rem;text-align:center;margin-top:3rem;font-family:"Courier New",monospace}@keyframes pulse{0%{transform:scale(1);opacity:1}50%{transform:scale(1.1);opacity:.7}100%{transform:scale(1);opacity:1}}